<template>
  <div class="coming">
    <!-- this is coming components -->
    <movie-list :list="comingList"
                ref="movie"
                @loadMoreData="loadMore">
      <expect-list :list="expectList" />
    </movie-list>
  </div>
</template>

<script>
import MovieList from '@/components/Content/MovieList'
import ExpectList from '@/components/Content/ExpectList'

import axios from 'axios'
import { getComingList, getMostExpected,getComingMoreList } from '@/network/home.js'

export default {
  name: 'coming',
  props: [''],
  data () {
    return {
      comingList: [],
      expectList: [],
    }
  },

  components: {
    MovieList,
    ExpectList
  },

  computed: {},

  beforeMount () { },

  mounted () { },

  methods: {
    /**
     * 获取即将上映电影列表数据
     */
    getComingList () {
      getComingList().then(res => {
        this.comingList = res.coming
      })
    },

    /**
     * 获取近期最受期待电影列表
     */
    getMostExpected () {
      getMostExpected().then(res => {
        this.expectList = res.coming
      })
    },

    /**
     * MovieList加载更多事件
     */
    loadMore () {
      getComingMoreList().then(res => {
        this.comingList.push(...res.coming)

        // 执行回调事件
        this.$refs.movie.loadCallback();
      })
    },

  },

  watch: {},

  created () {
    this.getComingList()
    this.getMostExpected()
  }
}
</script>
<style scoped>
.coming {
  background: #f5f5f5;
}
</style>
